.navbar_link{color:#000;text-decoration:none}.navbar_link:hover{text-decoration:none;color:#7c7c7c}.navbar_link--active{color:#707070}li{margin-right:25px;font-size:26px}ul{-webkit-padding-start:0;padding-inline-start:0}.scuba-search{background-color:#e7e7e7;color:#000;border:#e7e7e7 2px solid;width:8em;height:2.2em}.scuba-search:focus{outline:none;background-color:#fcfcfc}@media screen and (min-width: 1600px){.scuba-search{width:16em}}.search-button{background-color:#f5f5f5;border:#e7e7e7 solid 2px;color:black;height:2.2em;width:3.5em}.login-button{background-color:#e7e7e7;background-image:url(data:image/png;base64,);height:2em;width:4em}@media screen and (min-width: 1600px){.login-button{width:7em}}.login-text{padding-left:1em;padding-top:0.2em;font-size:1.2em}


.hero-text{background-color:rgba(255,255,255,0.7);text-align:center;position:absolute;top:236px;left:30%;padding:2% 8%}.quote-text{font-size:1.2rem}.scuba-bg{background-image:url(/static/media/scuba2.85aeb23d.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;min-height:70vh}

