.bg-black{background-color:black}

.testbox{outline:solid black 1px;outline-offset:-3rem}.img-padi{width:20rem;height:auto}.long-text{font-size:2rem}@media screen and (min-width: 472px){.long-text{font-size:2.5rem}}



